.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

p {
  margin:0 0 1px;
}

.good{
  background-color:rgb(244, 244, 66);
}

.bad{
  background-color:rgb(244, 128, 65);
}

.paragraph_detail{
  float:right;
  width:240px; 
  display:none;
  font-size:10px;
  font-style: italic;
  background-color:#e5af43;
  padding:5px;
  position:absolute;
  border: 3px solid #f44141;
}

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}

div.textunit{
  background-color:#41f4e5;
  margin:0 0 1px;
}

div.summary{
  #background-color:rgb(244, 65, 220);
  padding:5px;
}

.key_difference {
  font-style: italic;
  font-size:12px;
  color:rgb(48, 175, 229);
}


.button {
    background-color: #008CBA; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    margin: 4px 2px;
    border-radius: 5px;
    cursor: pointer;
}

.button2 {
   border-radius: 5px;
   background-color:coral;
}

.button3 {
  background-color:grey;
}

p span:hover {
  background-color : yellow
}

p.summary{
  padding:5px;
}

.searchbar {
  text-align: center;
  font-size: 17px;
  padding-top:10px;
}

input {
  padding:5px;
}

.headline{
  text-align:center
}

select {
   color: #fff;
   background-color: coral;
   border-radius: 5px;
   padding-left: 15px;
}

label {
  margin:10px;
  color:grey;
  font-style:italic bold;
}

label.note{
  color:grey;
  font-size:8px;
  font-style:italic;
}

.stats{
  margin-top:10px;
  font-size:12px;
}

.column-results{
  margin-top:20px;
}


